.App {
    font-family: 'Lato',arial,sans-serif;
    color: #434343;
    background: #dae3e7;
    font-size: 16px;
    -webkit-font-smoothing: antialiased;  
}
.header {
    padding: 30px 0;
    background: #f5f5f5;
    border-top: 10px solid #778492;
}
.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
.section {
    margin-bottom: 30px;
}
.sections-wrapper {
    padding-top: 60px;
    padding-bottom: 60px;
}
.section .section-inner {
    background: #fff;
    padding: 30px;
}
.row {
    margin-right: -15px;
    margin-left: -15px;
}